@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
.logo {
    display: flex;
    align-items: center;
    font-size: 27px;
}

.logo img {
    height: 3rem;
    width: 3rem;
}

.logo span {
    font-weight: 800;
    color: var(--color-green);
}

.logo-link {
    padding-left: 2px;
    text-decoration: none;
    color: #000;
}

.navbar {
    display: flex;
    align-items: center;
    padding: 18px 30px;
    background-color: #fefefe;
}

.navbar.navbar-sticky {
    position: sticky;
    top: 0;
    z-index: 1;
}

nav ul {
    list-style: none;
}

.navbar .nav-links {
    margin-left: auto;
    font-size: 14px;
}

nav ul li {
    display: inline-flex;
}

.navbar .nav-link {
    margin: 0px 16px;
    color: #000;
    text-decoration: none;
}

nav ul .nav-link::after {
    content: '';
    display: block;
    height: 2px;
    width: 0px;
    background-color: #000;
    transition: width 0.3s;
}

nav ul .nav-link:hover::after {
    width: 100%;
    transition: width 0.3s;
}

.btn-dropdown {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 1.9rem;
    right: 2.4rem;
    height: 21px;
    width: 30px;
}

.btn-dropdown .dropdown-bar {
    height: 2px;
    width: 100%;
    background-color: #000;
}

@media (max-width: 1050px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
    }

    nav ul {
        position: absolute;
        width: 190px;
        top: 4.8rem;
        right: 1.8rem;
        padding: 10px 0;
        background-color: #fff;
        z-index: 1;
        border-radius: 15px;
        box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.7);
        transition: width 2s, height 2s, background-color 2s, -webkit-transform 2s;
        transition: width 2s, height 2s, background-color 2s, transform 2s;
        transition: width 2s, height 2s, background-color 2s, transform 2s, -webkit-transform 2s;
    }

    nav ul li {
        display: block;
        padding: 13px 0;
        background-color: #fff;
        position: relative;
        z-index: 0;
    }

    .nav-links {
        display: none;
    }

    .nav-links.dropdown {
        display: inline;
    }

    .navbar .nav-link {
        display: block;
    }

    .btn-dropdown {
        display: flex;
    }

    .dropdown-bar.dropdown-clicked {
        height: 4px;
        background-color: var(--color-green);
    }
}

@media (max-width: 500px) {
    nav ul {
        position: absolute;
        width: 50%;
        top: 4.8rem;
        right: 1.8rem;
    }
}

